.theme-dark {

  --dark-color-1: #333333;
  --dark-color-2: #222222;
  --dark-color-3: #111111;

  --mid-color-1: #777777;
  --mid-color-2: #9b9b9b;
  --mid-color-3: #aaaaaa;

  --light-color-1: #cccccc;
  --light-color-2: #ededed;
  --light-color-3: #fafafa;

  --bkg-color-1: var(--dark-color-1);
  --bkg-color-2: var(--dark-color-2);
  --bkg-color-3: var(--dark-color-3);

  --fwd-color-1: var(--light-color-1);
  --fwd-color-2: var(--light-color-2);
  --fwd-color-3: var(--light-color-3);

  --ms-color-1: #ff9922;
  --ms-color-2: #ff8000;
  --ms-color-3: #df6000;

  --efg-color-1: #2299ff;
  --efg-color-2: #0080ff;
  --efg-color-3: #0060df;

  --dip-color-1: #00ff80;
  --dip-color-2: #00df60;
  --dip-color-3: #00bb50;

  --jcoup-color-1: #ff2299;
  --jcoup-color-2: #ff0080;
  --jcoup-color-3: #df0060;

  --spinsys-color-1: #9467bd;
  --spinsys-color-2: #7b5ea6;
  --spinsys-color-3: #63458f;
  

  --ok-color-1: #22ff33;
  --ok-color-2: #00cc22;
  --ok-color-3: #00aa10;

  --err-color-1: #ff3322;
  --err-color-2: #cc2200;
  --err-color-3: #aa1000;

  --overlay-color: #111111aa; 
}

.theme-light {

  --dark-color-1: #333333;
  --dark-color-2: #222222;
  --dark-color-3: #111111;

  --mid-color-1: #777777;
  --mid-color-2: #666666;
  --mid-color-3: #555555;

  --light-color-1: #bbbbcc;
  --light-color-2: #dadae5;
  --light-color-3: #ffffff;

  --bkg-color-1: var(--light-color-1);
  --bkg-color-2: var(--light-color-2);
  --bkg-color-3: var(--light-color-3);

  --fwd-color-1: var(--dark-color-1);
  --fwd-color-2: var(--dark-color-2);
  --fwd-color-3: var(--dark-color-3);

  --ms-color-3: #ff9922;
  --ms-color-2: #ff8000;
  --ms-color-1: #df6000;

  --efg-color-3: #2299ff;
  --efg-color-2: #0080ff;
  --efg-color-1: #0060df;

  --dip-color-3: #00ff80;
  --dip-color-2: #00df60;
  --dip-color-1: #00bb50;

  --jcoup-color-3: #ff2299;
  --jcoup-color-2: #ff0080;
  --jcoup-color-1: #df0060;

  --spinsys-color-3: #9467bd;
  --spinsys-color-2: #7b5ea6;
  --spinsys-color-1: #63458f;
  
  --ok-color-1: #00aa10;
  --ok-color-2: #00cc22;
  --ok-color-3: #22ff33;

  --err-color-1: #aa1000;
  --err-color-2: #cc2200;
  --err-color-3: #ff3322;

  --overlay-color: #111111aa; 
}

.mv-main-app {
  /* Defines variables that will be used app-wide */
  --ctrl-border-width: 1px;
  --ctrl-border-radius: 5px;
  --ctrl-padding: 8px;
}

/* Basic MVControl class */
.mv-control {
  font-size: 0.95rem;

  border-width: var(--ctrl-border-width);
  border-radius: var(--ctrl-border-radius);
  border-color: var(--fwd-color-1);

  padding: var(--ctrl-padding);

  font-family: "Open Sans", sans-serif;
}

.mv-control:focus {
  outline: none;
}

.mv-main-app {
  /* Shared variables */
  --header-height: 5em;
  --sidebar-width: 25em;
}

.mv-main-page {
  color: var(--fwd-color-2);
}

.mv-background {
  width: 100vw;
  height: 100vh;
  background-color: #333333;
}

.mv-main-page .drag-overlay {
  position: fixed;
  background-color: white;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s;
}

.mv-main-page.has-drag .drag-overlay {
  opacity: 0.2;
}

::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--bkg-color-1);
}

/* Handle */
::-webkit-scrollbar-thumb {
  border: 3px var(--bkg-color-1) solid;
  background-clip: padding-box;
  background: var(--fwd-color-1);
  border-radius: 9999px;
  width: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--fwd-color-3);
}

.mv-header {
  background-color: var(--bkg-color-3);
  position: fixed;
  top: 0;
  width: 100vw;
  height: var(--header-height);
  border-bottom: 1px solid var(--mid-color-1);
  z-index: 999;

  -webkit-user-select: none; /* Safari */ /* Firefox */ /* IE10+/Edge */
  user-select: none; /* Standard */
}

.mv-header-left {
  float: left;
  height: 100%;
  padding-left: 1em;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.mv-header-right {
  float: right;
  height: 100%;
  padding-right: 1em;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

#mv-header-logo {
  height: calc(var(--header-height) * 0.7);
  padding: 0.3em;
  -webkit-animation: logo-spin 1s ease 0s 1;
          animation: logo-spin 1s ease 0s 1;
  margin-right: 0.5em;
}

#mv-header-title {
  font-family: Lato, sans-serif;
  font-weight: var(--font-heavy);
  font-size: calc(var(--header-height) * 0.5);
  margin: 0;
}

#mv-themeswitch {
  --switch-size: calc(var(--header-height) * 0.35);

  overflow: hidden;
  font-size: calc(var(--header-height) * 0.3);
  width: var(--switch-size);
  height: var(--switch-size);
  border-radius: var(--switch-size);
  background-color: var(--bkg-color-2);
  transition: background-color 0.2s;
}

#mv-themeswitch:hover {
  background-color: var(--bkg-color-1);
}

#mv-themeswitch #mv-themeicons {
  position: relative;
  height: 100%;
  left: 0;
  width: calc(2 * var(--switch-size));

  box-sizing: border-box;

  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: var(--switch-size) var(--switch-size);

  transition: left 0.3s;
}

#mv-themeswitch #mv-themeicons.light {
  left: calc(var(--switch-size) * (-1));
}

@-webkit-keyframes logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.mv-cselect {
  --cselect-opt-height: 40px;
  --cselect-icon-size: 40px;
  --cselect-z-index: 999;
  max-height: var(--cselect-opt-height);

  z-index: var(--cselect-z-index);
}

/* If we have several on one page, we want to order the z-index */
.mv-cselect-1 {
  z-index: var(--cselect-z-index);
}
.mv-cselect-2 {
  z-index: calc(var(--cselect-z-index) - 1);
}
.mv-cselect-3 {
  z-index: calc(var(--cselect-z-index) - 2);
}
.mv-cselect-4 {
  z-index: calc(var(--cselect-z-index) - 3);
}


.mv-cselect-closed {
  pointer-events: none;
}

.mv-cselect-main {
  padding: 0;
  display: grid;
  grid-template-columns: auto var(--cselect-icon-size);
  align-items: center;
  background-color: var(--fwd-color-3);
  pointer-events: all;
}

.mv-cselect-main-caret {
  color: var(--bkg-color-3);
  padding-left: 15px;
}

.mv-cselect-ddown {
  padding: 0;
  padding-right: var(--cselect-icon-size);
  grid-column: 1 / 3;
  background-color: var(--fwd-color-3);
  box-shadow: 2px 2px 2px var(--bkg-color-1);
}

.mv-cselect-closed .mv-cselect-ddown {
  visibility: hidden;
  z-index: -9999;
}

.mv-cselect-opt {
  height: var(--cselect-opt-height);
  font-size: 1.1em;
  box-sizing: border-box;
  min-width: 150px;
  color: var(--bkg-color-3);
  background-color: var(--fwd-color-3);
  transition: background-color 0.2s;

  cursor: pointer;
  display: grid;
  align-items: center;
  grid-template-columns: 30px auto;
  position: relative;
}

.mv-cselect-opt:hover {
  background-color: var(--fwd-color-2);
}

.mv-cselect-disabled .mv-cselect-opt {
  color: var(--mid-color-1);
}

.mv-cselect-disabled .mv-cselect-opt:hover {
  background-color: var(--fwd-color-3);
}


.mv-cselect-disabled .mv-cselect-main-caret {
  color: var(--mid-color-1);
}

.mv-icon path {
  fill: var(--path-fill) !important;
}

.mv-sshot {
    color: var(--fwd-color-2);

    position: fixed;
    bottom: 20px;
    left: 20px;

    font-size: 2em;
    cursor: pointer;

    opacity: 0.5;
    transition: opacity 0.5s;

    display: flex;
    align-items: center;
}

.mv-sshot:hover {
    opacity: 0.9;
}

.mv-sshot-text {
    font-size: 0.5em;
    margin-left: 5px;

    display: none;
}

.mv-sshot:hover .mv-sshot-text {
    display: block;
}

.mv-sshot a {
    display: none;
}
.mv-sidebar {
  position: fixed;

  top: 0;
  height: 100vh;
  width: var(--sidebar-width);
  background: var(--bkg-color-3);
  padding-top: calc(var(--header-height) + 20px);
  padding-left: calc(var(--sidebar-width) * (0.05));
  padding-right: calc(var(--sidebar-width) * (0.03));
  border-left: 1px solid var(--mid-color-1);
  box-sizing: border-box;
  overflow-y: auto;

  /* Animate sliding in when opening */
  right: calc(var(--sidebar-width) * (-1));
  transition: right 0.2s;
}

.mv-sidebar.open {
  right: 0;
}

.mv-sidebar .mv-sidebar-title {
  margin-top: 0;
  margin-right: 1rem;
  margin-bottom: 0.8rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid var(--mid-color-1);

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.mv-sidebar-title h2 {
  font-size: 1.2rem;
  margin: 0;
  padding: 0;
}

.mv-sidebar-hide {
  cursor: pointer;
}

.mv-sidebar p {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.mv-sidebar .mv-sidebar-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;  
}

.mv-sidebar .mv-sidebar-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.mv-sidebar .mv-sidebar-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: stretch;
}

.mv-sidebar .mv-sidebar-tooltip-grid {
  display: grid;
  grid-template-columns: 1fr 0.1fr;
  align-items: stretch;
}

.mv-sidebar .sep-1 {
  display: block;
  margin-bottom: 1em;
}

.mv-sidebar::-webkit-scrollbar {
  width: 15px;
  margin-top: var(--header-height);
}

.mv-sidebar::-webkit-scrollbar-track-piece:start {
  background: transparent;
  margin-top: var(--header-height);
}

.mv-sidebar .hidden {
    display: none !important;
}

.mv-warning-noms {
    font-weight: bold;
    font-size: 3rem;
    text-align: center;
    margin-top: 3rem;
    color: var(--err-color-2);
}
.mv-file::file-selector-button {
  min-width: 70px;

  color: var(--dark-color-3);
  background-color: var(--light-color-3);
  border-color: var(--fwd-color-3);
  transition: background-color 0.2s;

  border-width: var(--ctrl-border-width);
  border-radius: var(--ctrl-border-radius);
  border-color: var(--fwd-color-1);
  margin-right: 0.5rem;

  padding: var(--ctrl-padding);

  font-family: "Open Sans", sans-serif;
}

.mv-file::file-selector-button:focus {
  outline: none;
}

.mv-file::file-selector-button:hover {
  background-color: var(--light-color-2);
}

.mv-file::file-selector-button:active {
  background-color: var(--light-color-3);
}

.mv-file::file-selector-button:disabled {
  color: var(--mid-color-2);
}

.mv-file::file-selector-button:disabled:hover {
  background-color: var(--light-color-3);
}

.mv-file.hide-text {
  color: #00000000;
}
.mv-box {
    display: none;
    width: 100%;
    box-sizing: border-box;
    color: var(--bkg-color-3);
}

.mv-box-success {
    display: block;
    background-color: var(--ok-color-1);
    box-shadow: 0 2px 5px var(--ok-color-3);
}

.mv-box-error {
    display: block;
    background-color: var(--err-color-1);
    box-shadow: 0 2px 5px var(--err-color-3);
}

.mv-box .mv-box-header {
    text-align: right;
    cursor: pointer;
    padding-bottom: 5px;
    margin-bottom: 5px;
}
.mv-checkbox {
  display: flex;
  /* justify-content: center; */
  align-items: center;

  --check-color: var(--ms-color-2);
}

.mv-checkbox input {
  opacity: 0;
  width: 0px;
  box-sizing: border-box;
  margin: 0;
}

.mv-checkbox label {
  width: 6px;
  height: 6px;

  margin-right: 8px;
  padding: 5px;
  border-radius: 5px;

  border: 3px solid var(--fwd-color-2);
  background-color: var(--fwd-color-2);
  transition: background-color 0.2s;
}

.mv-checkbox :checked + label {
  background-color: var(--check-color);
}

.mv-lselect {

    background-color: var(--light-color-3);
    color: var(--dark-color-3);
    border: 1px solid var(--fwd-color-3);

    --lselect-opt-height: 40px;

    height: calc(var(--lselect-opt-height)*5);

    overflow-y: auto;
}

.mv-lselect-opt {
  height: var(--lselect-opt-height);
  /* font-size: 0.95em; */
  box-sizing: border-box;
  min-width: 150px;
  color: var(--dark-color-3);
  background-color: var(--light-color-3);
  transition: background-color 0.2s;

  cursor: pointer;
  display: grid;
  align-items: center;
  grid-template-columns: 30px auto 30px 30px;
}

.mv-lselect-opt:hover {
  background-color: var(--light-color-2);
}

.mv-lselected {
  background-color: var(--light-color-1);    
}

.mv-lselected:hover {
  background-color: var(--light-color-1);    
}
/*  'tooltip-container' */

.mv-tooltip {
    display: inline-flex;
    justify-content: flex-start;
  width: 100%;
}

.tooltip-container {
    z-index: 10000;
}
.mv-range {
  --track-color: var(--fwd-color-2);
  --thumb-color: var(--ms-color-2);
  --outline-color: var(--ms-color-2);

  display: inline-flex;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  padding: var(--ctrl-padding) 0;
}

/* CSS generated with the help of http://danielstern.ca/range.css/#/ */

input[type=range].mv-range-slider {
  width: 80%;
  box-sizing: border-box;
  margin: 0;
  margin-right: 10px;
  background-color: transparent;
  -webkit-appearance: none;  
}
input[type=range].mv-range-slider:disabled {
  --track-color: var(--light-color-1);
}
input[type=range].mv-range-slider:focus {
  outline: none;
}
input[type=range].mv-range-slider::-webkit-slider-runnable-track {
  background: var(--track-color);
  border: 4px solid var(--track-color);
  border-radius: 4px;
  width: 100%;
  height: 30px;
  cursor: pointer;
}
input[type=range].mv-range-slider::-webkit-slider-thumb {
  margin-top: 0px;
  width: 22px;
  height: 22px;
  background: var(--thumb-color);
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  -webkit-appearance: none;
}
input[type=range].mv-range-slider:disabled::-webkit-slider-thumb {
  -webkit-filter: saturate(50%);
          filter: saturate(50%);
}
input[type=range].mv-range-slider:focus::-webkit-slider-runnable-track {
  background: var(--track-color);
}
input[type=range].mv-range-slider::-moz-range-track {
  background: var(--track-color);
  border: 4px solid var(--track-color);
  border-radius: 4px;
  width: 100%;
  height: 30px;
  cursor: pointer;
}
input[type=range].mv-range-slider::-moz-range-thumb {
  width: 22px;
  height: 22px;
  background: var(--thumb-color);
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 4px;
  cursor: pointer;
}
input[type=range].mv-range-slider:disabled::-moz-range-thumb {
  filter: saturate(50%);
}
input[type=range].mv-range-slider::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 0 0;
  color: transparent;
  width: 100%;
  height: 30px;
  cursor: pointer;
}
input[type=range].mv-range-slider::-ms-fill-lower {
  background: var(--track-color);
  border: 4px solid var(--track-color);
  border-radius: 8px;
}
input[type=range].mv-range-slider::-ms-fill-upper {
  background: var(--track-color);
  border: 4px solid var(--track-color);
  border-radius: 8px;
}
input[type=range].mv-range-slider::-ms-thumb {
  width: 22px;
  height: 22px;
  background: var(--thumb-color);
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type=range].mv-range-slider:disabled::-ms-thumb {
  filter: saturate(50%);
}
input[type=range].mv-range-slider:focus::-ms-fill-lower {
  background: var(--track-color);
}
input[type=range].mv-range-slider:focus::-ms-fill-upper {
  background: var(--track-color);
}
/*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
how to remove the virtical space around the range input in IE*/
@supports (-ms-ime-align:auto) {
  /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
  input[type=range].mv-range-slider {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}
.mv-text {
  display: flex;
  flex-direction: column;
}

.mv-textlabel {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.mv-textfield {
  --outline-color: var(--ms-color-2);
  box-sizing: border-box;
  border: 2px solid var(--fwd-color-3);
}

.mv-textfield.mv-submit-wait {
  border: 2px solid var(--outline-color);
}

.mv-textfield:disabled {
  background-color: var(--light-color-1);
}

.mv-button {
  min-width: 70px;
  width: 100%;

  color: var(--dark-color-3);
  background-color: var(--light-color-3);
  border-color: var(--fwd-color-3);
  transition: background-color 0.2s;
}

.mv-button:hover {
  background-color: var(--light-color-2);
}

.mv-button:active {
  background-color: var(--light-color-3);
}

.mv-button:disabled {
  color: var(--mid-color-2);
}

.mv-button:disabled:hover {
  background-color: var(--light-color-3);
}

.mv-radiogroup {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  --check-color: var(--ms-color-2);
}

.mv-radiogroup > .mv-radio {
  margin-left: 1rem;
}

.mv-radiogroup-label {
  font-weight: bold;
}

.mv-radio {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.1rem;
}

.mv-radio > input {
  opacity: 0;
  width: 0px;
  box-sizing: border-box;
  margin: 0;
}

.mv-radio > label {
  width: 3px;
  height: 3px;

  margin-right: 8px;
  padding: 5px;

  border: 3px solid var(--fwd-color-2);
  background-color: var(--fwd-color-2);
  border-radius: 10px;
}

.mv-radio > :focus + label {
  outline: none;
}

.mv-radio > :checked + label {
  background-color: var(--check-color);
}

.mv-msref-table {
    display: flex;
    flex-direction: column;
}

.mv-msref-table-row {
    display: grid;
    grid-template-columns: 3fr 5fr 4fr;

    min-width: 150px;
    padding: 10px 5px;
}

.mv-msref-table-el {
    display: flex;
    flex-direction: column;
    justify-content: center;

    font-weight: bold;
    font-size: 1.2em;
}

.mv-msref-table-ref {
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding-right: 5px;
}
.mv-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;    

    width: 100vw;
    height: 100vh;
    margin: 0;
    box-sizing: border-box;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: var(--overlay-color);
}

.mv-modal-overlay.mv-modal-hidden {
    display: none;
}

.mv-modal {
    color: var(--fwd-color-1);
    background-color: var(--bkg-color-1);

    min-width: 30vw;
    min-height: 30vh;
    max-width: 100vw;
    max-height: 100vh;
    
    box-shadow: 0 2px 5px var(--fwd-color-3);

    resize: both;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

:not(.mv-modal-overlay) > .mv-modal {
    position: fixed;
    top: 100px;
    left: 100px;

    z-index: 9999;
}

.mv-modal-resizable {
    resize: both;
    overflow: hidden;
}

.mv-modal-title {
    padding: 10px 0px;
    border-bottom: 1px solid var(--fwd-color-1);
    margin-bottom: 10px;

    font-weight: bold;
    text-align: center;

    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;

    -webkit-user-select: none; /* Safari */ /* Firefox */ /* IE10+/Edge */
    user-select: none; /* Standard */
}

.mv-modal-draggable .mv-modal-title {
    cursor: move;
}

.mv-modal-title svg {
    cursor: pointer;
}

.mv-modal-content {
    display: grid;
    align-items: center;
    justify-content: center;
    max-height: 70vh;
    overflow-y: auto;
}

.mv-modal-resizable .mv-modal-content {
    overflow: auto;    
    width: 100%;
    height: 100%;
}

.mv-modal-footer {
    display: flex;
    align-items: center;
    justify-content: center;
}
/* the actual color bar */
.mv-cscale-bar {
    width: 100%;
    height: 1em;
}

.mv-cscale-tick {
    /* center in td */
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-33%);
            transform: translateY(-33%);
    text-align: center;
    height: 0.5em;
    
}

.mv-cscale-tick-label {
    vertical-align: top;
    font-size: 0.8em;
}


/* the table that holds the color bar */
.mv-cscale-table {
    width: 90%;
    height: 1em;
    table-layout: fixed;
    /* no border */
    border-spacing: 1px;
    vertical-align: top;
}



.mv-euler-agrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    width: 80%;
}

.mv-euler-agrid span {
    text-align: right;
}

.mv-euler-agrid .header {
    font-weight: bold;
    text-align: left;
}

.mv-euler-agrid-switch {
    grid-column: 1 / span 2;

    font-weight: bold;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.mv-euler-agrid-switch span {
    padding: 0 10px;
}

.mv-eul-results {
    width: 90%;
    background-color: var(--bkg-color-1);
    box-sizing: border-box;
    table-layout: fixed;
    z-index: -1;
}

.mv-eul-results thead {
    background-color: var(--bkg-color-2);
}

.mv-eul-results td {
    padding: 5px;
}

.mv-eul-results tbody {
    text-align: right;
}
.mv-switch {
    position: relative;

    cursor: pointer;

    width: 60px;
    height: 30px;
    border-radius: 15px;
    box-sizing: border-box;
    padding: 0;

    transition: background-color 0.3s;
}

.mv-switch-true {
    background-color: var(--color-true);    
}

.mv-switch-false {
    background-color: var(--color-false);    
}

.mv-switch-thumb {
    position: absolute;
    background-color: var(--fwd-color-2);

    width: 30px;
    height: 30px;
    border-radius: 15px;
    padding: 0;
    box-sizing: border-box;

    transition: -webkit-transform 0.3s;

    transition: transform 0.3s;

    transition: transform 0.3s, -webkit-transform 0.3s;
}

.mv-switch-true .mv-switch-thumb {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
}
.mv-plots-agrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    width: 80%;
}

.mv-plots-agrid span {
    text-align: right;
}

.mv-plots-agrid .header {
    font-weight: bold;
    text-align: left;
}

.mv-plots-agrid-switch {
    grid-column: 1 / span 2;

    font-weight: normal;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.mv-plots-agrid-switch span {
    padding: 0 10px;
}

.img-layer-margins rect {
    fill: #ee5500;
}

.img-layer-margins .selected rect {
    stroke-width: 2px;
    stroke: #00ccee;
}
